// -----------------------------------------------------------------------------
// _base.scss
// This file contains very basic styles to reset document.
// -----------------------------------------------------------------------------

// Global
// =========================================================================

html {
	box-sizing: border-box;
	scroll-behavior: smooth;
}

html,
body {
	height: 100%;
	width: 100%;
	margin: 0;
	padding: 0;
	font-family: $font;
	font-size: 1rem;
	line-height: 1.6;

	font-kerning: normal;
	-webkit-font-smoothing: antialiased; // sass-lint:disable-line  no-vendor-prefixes
	-webkit-tap-highlight-color: transparent; // sass-lint:disable-line  no-vendor-prefixes
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

body {
	background-color: $black;

	&.theme-white {
		background-color: #{$white};
		--colour-background: #{$white};
		--colour-foreground: #{$black};
		--color-mark: rgba(#{$white}, 0.15)
	}
}

.content-wrapper {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	min-height: 100%;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

// Resets
// =========================================================================

figure {
	margin: 0;
}

ul {
	margin: 0;
	padding: 0;
	list-style-type: none;
}

// No JS
// =========================================================================

.no-js {

	.js-hide {
		display: none !important;
	}

	.lazy {
		display: none !important;
	}
}

// Mods
// =========================================================================

.js-hide {
	display: none !important;
}
